
.box-it {
  border-style: solid;
  border-width: 1px;
  border-color: black;
  padding: 12px;
  margin: 2px;
}
.fluid-btn{
  color: white;
  background-color: #2c55ba;
  border: none;
  padding: 5px 12px;
  width: 100%;
  cursor: pointer;
}

.accordion .card:first-of-type {
  border-bottom-width: 1px !important;
  border-bottom-style: solid;
  border-bottom-color: #dfdfdf;
}
.status-container {
  display: -webkit-flex;
  display: flex;
  margin-right: 12px;
}
.status-details-container {
  width: 380px;
}
.upload-text {
  font-size: 18px;
}
.firm-card {
  margin: auto;
}

.update-row {
  border-style: solid;
  border-width: 1px;
  padding: 14px 0px;
  margin: 0px;
}
.toggle-card {
  margin: 0px !important;
}
.toogle-footer {
  padding: 5px
}
.upload-container {
  margin: 10px auto;
  border-style: solid;
  border-width: 1px;
  padding: 10px 0px 10px 20px;
  background-color: #2c55ba14;
}
.mb-3 {
  margin: 0px !important;
}
.table-container {
  margin: 10px auto;
}
.copy-firm{
  width:18px;
  height:18px
}

.cust-home {
  margin: 10px auto;
  width: 800px;
  padding: 20px;
}

.custom-header {
  background: #337ab7;
  font-size: 18px;
  color: white;
}
.card-container {
  font-size: 14px;
  margin: 10px auto;
}
.img-upload {
  width: 28px;
  height: 28px;
  margin: auto 12px;
  cursor: pointer;
}
.modal-header {
  padding: 10px 10px;
}
.upload-text {
  margin: 0px;
}

.close-firm, .send-firm, .upload-firm, .refresh-firm {
  height: 32px;
  width: 32px;
  cursor: pointer;
  margin-right: 10px !important;
}
.cust-btn {
  color: white;
  background-color: #2c55ba;
  border: none;
  padding: 5px 12px;
  cursor: pointer;
  margin-right: 20px !important;
}
.modal-footer > :only-child {
  margin-right: 20px
}
